
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'


export default {
name: 'BetHeader',
components: {
XHeader,
Popup
},
props: ['method', 'curMethod', 'curSecond'],
computed: {
gameType() {
return this.$store.state.bet.gameType;
}
},
async created() {
await this.service();
},
data() {
return {
show: false,
first: 'default',
serviceLink: '',
gameTypeDecLink:
SETTING.apiHost +
'/gameType/' +
this.$store.state.bet.gameType +
'.html',
moreHelpShow: false
};
},
methods: {
showPopup() {
if (this.curMethod != 'defalut') {
this.first = this.curMethod;
}
this.show = !this.show;
},
check(typeDetId) {
this.$emit('check', typeDetId);
this.show = false;
},

checkFirst(it) {
this.first = it.proType;
},

checkSecond(it) {
this.$emit('checkSecond', this.method[this.first], it);
this.show = false;
},

showMoreHelp() {
console.log(1);
this.moreHelpShow = !this.moreHelpShow;
},

async service() {
let res = await this.$http('/queryCustomerServiceInfo');
console.log(res.returnMap.customerServiceUrl);
this.serviceLink = res.returnMap.customerServiceUrl;
}
},
watch: {
curMethod() {
if (this.curMethod != 'defalut') {
this.first = this.curMethod;
}
}
}
};



